
.demo-area{position:relative;padding:50px 50px 0 0
}
.demo-area .demo-title{margin-bottom:32px
}
.demo-area .preview-button{float:right;background-color:#3cbaff;color:#ffffff;height:40px;padding:0 18px;border-radius:4px
}
.demo-area .phone{position:relative;width:352px;height:690px;border-radius:32px;background:#637CA3
}
.demo-area .phone .btn-QR-code{position:absolute;top:16px;right:16px;display:block;padding:0 10px;line-height:32px;cursor:pointer;overflow:hidden;background-color:#3CBAFF;color:#ffffff;vertical-align:middle;border-radius:20px
}
.demo-area .phone .btn-QR-code img{max-width:24px
}
.demo-area .phone .btn-QR-code>*{vertical-align:middle
}
.demo-area .phone .QR-code-container,.demo-area .phone .demo-container{position:absolute;top:60px;left:16px;width:320px;height:570px
}
.demo-area .phone .QR-code-container{background:rgba(255,255,255,0.8);z-index:2;text-align:center
}
.demo-area .phone .QR-code-container .QR-code{position:absolute;top:50%;width:100%;margin-top:-80px
}
.demo-area .phone .QR-code-container .QR-code canvas{width:140px;height:140px;margin:0 auto
}
.demo-area .phone .demo-container{background:#EAEFF3
}
.demo-area .phone .demo-container iframe{width:100%;height:100%
}
.switch-demo-area{position:absolute;width:20px;height:20px;top:50%;margin-top:-10px;left:-10px;background-color:#3cbaff;cursor:pointer
}
@media screen and (max-width:1300px){
.demo-area{display:none
}
}


